.CreateTodoButton{
        background-color:hsl(51, 100%, 50%);
        box-shadow:0 5px 25px hsla(51, 100%, 34%, 0.877);
        border: none;
        border-radius: 50%;
        cursor: pointer;
        font-size: 50px;
        position: fixed;
        bottom: 24px;
        right: 24px;
        font-weight: bold;
        color: #FAFAFA;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 64px;
        width: 64px;
        z-index: 1;
        transform: rotate(0);
        transition: .3s ease;
}

    .CreateTodoButton:hover{
        transform: rotate(224deg);

    }